@import "../libs";

.webtoon-detail-container {
	max-width: 50em;
	margin: 0 auto;
	padding-top: 70px;

	.webtoon-img {
		text-align: center;
		img {
			max-width: 480px;
			width: 100%;
		}
	}

	.webtoon-title,
	.webtoon-week,
	.webtoon-detail-btns {
		max-width: 480px;
		margin: 0 auto;
		padding: 0 1rem;
	}

	.webtoon-img {
		margin-bottom: 1rem;

		img {
			width: 480px;
			height: 480px;
			max-width: 480px;
			width: 100%;
		}
	}

	.webtoon-title {
		h3 {
			margin-bottom: 4px;
		}
		h5 {
			font-size: 1rem;
			margin-bottom: 8px;
		}
	}

	.webtoon-week {
		ul {
			display: flex;
			gap: 10px;
			margin-bottom: 1rem;

			li {
				font-size: 14px;
				background-color: $color-light-grey;
				border-radius: 12px;
				padding: 6px 10px;
			}
		}
	}

	.webtoon-detail-btns {
		display: flex;
		gap: 1rem;
		padding-bottom: 90px;

		.mywebtoon-save-btn,
		.webtoon-show-btn {
			flex: 1;
			border: 0;
			border-radius: 8px;
			color: $color-white;
		}

		.mywebtoon-save-btn {
			background-color: $color-primary;
		}

		.webtoon-show-btn {
			button {
				width: 100%;
				border: 0;
				border-radius: 8px;
				color: $color-white;
			}
		}

		.logo {
			svg {
				width: 30px;
			}
		}
	}
}
